.float-right{float: right;}
.hidden{display: none !important; outline: 0; -webkit-box-shadow: none; box-shadow: none;}
.disabled{pointer-events: none;}
.chromeless{border: none; -webkit-box-shadow: none; box-shadow: none; background: none; border-radius: 0; padding: 0; line-height: 20px;}
.chromeless:focus{outline: 0;}
.transparent{opacity: 0 !important; pointer-events: none;}
.invisible{visibility: hidden;}
*:focus{outline: none;}
